/*  	GENERAL												 */
/*************************************************************/
html,
body {
  font-family: Arial;
  color: #222;
  font-size: 16px;
  font-weight: normal; }

.left {
  float: left; }

.right {
  float: right; }

.clear {
  clear: both; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0; }

.half {
  width: 50%; }

.third {
  width: 33%; }

.two-thirds {
  width: 67%; }

.fourth {
  width: 25%; }

.three-fourths {
  width: 75%; }

.main {
  min-width: 300px; }

.container {
  max-width: 1200px;
  margin: 0 auto; }

